.wrapper {
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

p {
  margin-bottom: 1rem;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 8px;
  margin-right: 2rem;
  padding: .5rem 1rem;
  font-size: 3rem;
  transition: background-color .3s;
}

button:hover {
  background-color: #0056b3;
}

button:disabled {
  cursor: not-allowed;
  background-color: gray;
}

